@import url(https://fonts.googleapis.com/css2?family=Public+Sans:wght@400;500;600;700;800;900&display=swap%27);
:root {
	--blue: #1480ff;
	--blue-background: #1480ff1a;
	--red: #f06648;
	--red-background: #f066481a;
	--background: #eae5da;
}

[data-theme="dark"] {
	--blue: #3ea2ff;
	--blue-background: #3ea2ff1a;
	--red: #a8412a;
	--red-background: #a8412a1a;
	--background: #241e18;
}

body {
	color: #1480ff;
	color: var(--blue);
	background-color: #eae5da;
	background-color: var(--background);
	margin: 0;
	font-family: "Public Sans", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	transition: background-color ease 0.2s;
}

a {
	transition: color ease 0.2s;
}

a:link {
	color: #1480ff;
	color: var(--blue);
}
a:visited {
	color: #1480ff;
	color: var(--blue);
}
a:hover {
	color: #f06648;
	color: var(--red);
}
a:active {
	color: #f06648;
	color: var(--red);
}

.about-me-container {
	margin: 0;
	margin-bottom: 2em;
}

.about-me-container a {
	text-underline-offset: 2px;
}

.about-me-name {
	font-weight: 700;
}

.gregg-logo {
	margin-left: 0;
}

.talking-fox {
	margin-top: 2em;
	margin: 0em;
	display: flex;
	align-items: center;
	grid-gap: 0.5em;
	gap: 0.5em;
}

.bubble {
	position: relative;
	background-color: var(--red-background);
	color: var(--red);
	border: 1px solid #f06648;
	padding: 1em;
	border-radius: 10px;
	align-items: stretch;
	font-size: 1rem;
	transition: height 1s;
}
.bubble:after {
	content: "";
	position: absolute;
	display: block;
	width: 0;
	z-index: 1;
	border-style: solid;
	border-color: transparent #f06648;
	border-width: 8px 0 8px 8px;
	top: 50%;
	right: -8px;
	margin-top: -8px;
}

@media only screen and (min-width: 801px) {
	.about-me-container {
		margin-left: 0;
		font-size: 1.5rem;
		max-width: 450px;
	}

	.about-me-container a {
		text-underline-offset: 2px;
	}

	.about-me-name {
		font-weight: 700;
	}

	.gregg-logo {
		margin: 0;
	}
}

.event {
	background-color: var(--blue-background);
	border-radius: 10px;
	align-items: stretch;
	padding: 1em;
	border: 1px solid var(--blue);
	display: flex;
	flex-direction: column;
	grid-gap: 1.5em;
	gap: 1.5em;
}

.event-current {
	background-color: var(--red-background);
	color: var(--red);
	border-color: var(--red);
}

.event-header {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	grid-gap: 1em;
	gap: 1em;
}

.event-current-badge {
	background-color: var(--red);
	color: var(--background);
	border-radius: 20px;
	padding: 0.2em 0.5em;
	text-align: center;
	font-size: 0.85em;
}

.event-body {
	display: flex;
	flex-direction: column;
	grid-gap: 1em;
	gap: 1em;
}

.event-body p {
	margin: 0;
}

.event-body h3 {
	margin: 0;
}

.event-body > ul {
	list-style-type: none;
	padding: 0;
	margin: 0;
	display: flex;
	grid-gap: 0.5em;
	gap: 0.5em;
	flex-wrap: wrap;
}

.event-body > ul > li {
	font-size: 0.85em;
	border: 1px solid var(--blue);
	border-radius: 10px;
	padding: 0.2em 0.7em;
	background: var(--background);
	transition: background ease 0.2s;
}

.event-body-current > ul > li {
	border-color: var(--red);
}

.timeline-more-button {
	background-color: var(--blue-background);
	align-self: center;
	padding: 0.5em 1em;
	border-radius: 20px;
	color: var(--blue);
	font-size: 0.85em;
	font-family: inherit;
	border: 1px solid var(--blue);
	cursor: pointer;
}

.swirl-container {
	text-align: center;
}

@media only screen and (min-width: 801px) {
	.swirl-container {
		text-align: left;
	}
}

.navbar-container {
	display: flex;
	grid-gap: 1em;
	gap: 1em;
}

button.toggle {
	background: transparent;
	border: 0;
	color: var(--blue);
	font-weight: 700;
	padding: 0;
	font-size: 1em;
	transition: color ease 0.2s;
	font-family: inherit;
	cursor: pointer;
}

button.toggle:hover {
	color: var(--red);
}

button.toggle u {
	text-underline-offset: 2px;
}

#root {
	width: 100%;
}

/* Mobile */

#root > * {
	width: 100%;
	box-sizing: border-box;
}

header {
	justify-content: space-between;
	height: 100%;
	padding: 2em;
}

.timeline-section {
	display: flex;
	flex-direction: column;
	justify-content: center;
	padding: 1em 2em;
	grid-gap: 2em;
	gap: 2em;
}

/* Desktop */

@media only screen and (min-width: 801px) {
	#root > * {
		width: 50%;
		box-sizing: border-box;
	}

	header {
		float: left;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		height: 100%;
		position: fixed;
		padding: 3em;
	}

	.timeline-section {
		float: right;
		display: flex;
		flex-direction: column;
		justify-content: center;
		min-height: 100vh;
		padding: 3em 5em;
		grid-gap: 2em;
		gap: 2em;
	}
}

